.click-item {
  background-size: cover;
  background-position: 50%;
  height: 170px;
  width: 170px;
  margin: 8px 25px;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  cursor: pointer;
}
.wrapper {
  background: skyblue;
}

.header {
  background-color: skyblue;
  background-image: url("/assets/images/simpsons.jpg");
  background-size: 100%;
  color: #2A77AE;
  min-height: 150px;
  line-height: 150px;
  width: 100%;
  text-align: center;
  font-size: 4rem;
  text-shadow: 2px 2px black;
}
.score {
  background-color: skyblue;
  color: #2A77AE;
  text-align: center;
  font-size: 2rem;
}

ul {
  list-style-type: none;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.container {
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 100px;
  width: 1100px;
  margin: 0 auto;
}

.shake {
  -webkit-animation: shake .5s .0125s;
          animation: shake .5s .0125s;
}
